
import { defineComponent, ref, onMounted } from "vue";
import Dropdown2 from "@/components/dropdown/Dropdown2.vue";
import axios from "axios";
export default defineComponent({
  name: "kt-widget-2",
  components: {
    Dropdown2,
  },
  props: {
    widgetClasses: String,
  },
  computed: {
    currentRouteName() {
      return window.location.origin;
    },
  },
  setup() {
    const list = ref([]);

    // Fetch list of users from API
    const fetchUsers = async () => {
      try {
        const response = await axios.get(
          process.env.VUE_APP_API == "x"
            ? window.location.origin
            : process.env.VUE_APP_API + "/api/users"
        );
        const users = response.data;

        // Generate random avatar URL for each user
        users.forEach((user) => {
          const randomNumber = Math.floor(Math.random() * 100) + 1;
          user.avatar = `https://picsum.photos/id/${randomNumber}/300`;
        });

        // Update list of users
        list.value = users;
      } catch (error) {
        console.error(error);
      }
    };

    // Call fetchUsers on component mount
    onMounted(fetchUsers);

    return {
      list,
    };
  },
});
